import { createSlice } from "@reduxjs/toolkit";
import { server_messages_RU } from "../utils/text";
import { databaseAPI } from "../api/databaseAPI";
import { signInAPI } from "../api/authAPI";
var initialState = {
    error: "",
};
var getErrorMessage = function (error) {
    return server_messages_RU[error] || error;
};
var databaseErrorSlice = createSlice({
    name: "database-errors",
    initialState: initialState,
    reducers: {},
    extraReducers: function (builder) {
        builder.addMatcher(signInAPI.endpoints.login.matchPending, function (state, action) {
            state.error = "";
        });
        builder.addMatcher(databaseAPI.endpoints.loadData.matchRejected, function (state, action) {
            state.error = getErrorMessage(action.error.message || "");
        });
        builder.addMatcher(databaseAPI.endpoints.createRow.matchRejected, function (state, action) {
            state.error = getErrorMessage(action.error.message || "");
        });
        builder.addMatcher(databaseAPI.endpoints.updateRow.matchRejected, function (state, action) {
            state.error = getErrorMessage(action.error.message || "");
        });
        builder.addMatcher(databaseAPI.endpoints.deleteRow.matchRejected, function (state, action) {
            state.error = getErrorMessage(action.error.message || "");
        });
    },
});
export default databaseErrorSlice.reducer;
